<script lang="ts">
	import type { Color, TilePaintingSettings, TileType } from "../main";

	export let tileInfo: TileType;
	export let tilePaintingSettings: TilePaintingSettings;
	let beingPressed: boolean = false;

	function rgbToCss(rgba: Color) {
		return `rgba(${Math.floor(rgba.r * 255)}, ${Math.floor(rgba.g * 255)}, ${Math.floor(rgba.b * 255)}, ${rgba.a})`;
	}

	function clicked(e: MouseEvent) {
		if (e.pointerType !== "mouse" || e.button === 0) {
			// Set primary type.
			if (tilePaintingSettings.secondaryTileType === tileInfo.id) {
				// Type is already secondary! Swap with current secondary.
				tilePaintingSettings.secondaryTileType = tilePaintingSettings.primaryTileType;
			}
			tilePaintingSettings.primaryTileType = tileInfo.id;
		} else if (e.button === 2) {
			// Set secondary type.
			if (tilePaintingSettings.primaryTileType === tileInfo.id) {
				// Type is already primary! Swap with current primary.
				tilePaintingSettings.primaryTileType = tilePaintingSettings.secondaryTileType;
			}
			tilePaintingSettings.secondaryTileType = tileInfo.id;
		}
	}
</script>

<button
	id="outer"
	tabindex="0"
	on:click={clicked}
	on:contextmenu={(e) => e.preventDefault()}
	class:primary={tilePaintingSettings.primaryTileType === tileInfo.id}
	class:secondary={tilePaintingSettings.secondaryTileType === tileInfo.id}
>
	<div id="tile" style:background-color={rgbToCss(tileInfo.color)} />
	<!-- <div id="background" /> -->
	<div id="label-bg">
		<div id="label">{tileInfo.name}</div>
	</div>
</button>

