<script lang="ts">
	import MenuItemLabel from "./MenuItemLabel.svelte";
	export let label: string;
	export let value: number;
	export let softMin: number = 0;
	export let hardMin: number = 0;
	export let softMax: number = 1;
	export let hardMax: number = 1;
	export let step: number = 1;

	$: softMin = Math.max(softMin, hardMin);
	$: softMax = Math.min(softMax, hardMax);

	function validate(e) {
		value = Math.max(hardMin, Math.min(e.target.value, hardMax));
	}
</script>

<div id="outer">
	<div id="label-area">
		<MenuItemLabel name={label} />
	</div>
	<div id="input-area">
		<input class="slider" id="range" type="range" bind:value on:input={validate} min={softMin} max={softMax} {step} />
		<input class="number" id="number" type="number" bind:value on:input={validate} min={softMin} max={softMax} {step} />
	</div>
</div>

