	import type { TilePaintingSettings, TileType } from "../main";
	import InfoLine from "./InfoLine.svelte";
	import InfoPane from "./InfoPane.svelte";
	import MenuSection from "./MenuSection.svelte";
	import Range from "./RangeInput.svelte";
	import TextInput from "./TextInput.svelte";
	import TileTypeList from "./TileTypeList.svelte";

	export let tilePaintingSettings: TilePaintingSettings;
	export let tileTypes: TileType[];

	export let active: boolean;
	export let hide: boolean;
	export let game_info;

	let ui_scale = 1.0;
	let last_focused_element;
