<script lang="ts">
	import { onMount } from "svelte";
	import type { TilePaintingSettings, TileType } from "./main";
	import Menu from "./menu/Menu.svelte";

	/// Interface for running Rust functions.
	export let bindings: any;

	let canvasContainer: HTMLElement;
	let menuLayer: HTMLElement;
	let tileTypes: TileType[];
	let main: HTMLElement;

	let menu_active: boolean = true;

	let tilePaintingSettings: TilePaintingSettings = {
		primaryTileType: 2,
		secondaryTileType: 1,
		brushSize: 5,
	};

	let dragging_from_outside_menu = false;

	const ensureGameSettingsInitialized = () => {
		const waitForInit = (resolve: Function, reject: Function) => {
			if (bindings.is_game_initialized()) {
				resolve();
			} else {
				setTimeout(waitForInit.bind(this, resolve, reject), 30);
			}
		};

		return new Promise(waitForInit);
	};

	onMount(() => {
		// Start the program.
		bindings.run(canvasContainer);

		// Wait until the game is set up (async).
		ensureGameSettingsInitialized().then(function () {
			tileTypes = bindings.get_tile_types_info_wasm();

			const canvasResizeObserver = new ResizeObserver((es) => {
				for (const e of es) {
					bindings.set_canvas_size(e.contentRect.width, e.contentRect.height);
				}
			});
			canvasResizeObserver.observe(canvasContainer);
		});
	});

	const pointer_event_to_x_y = (pointer_event: PointerEvent): [number, number] => {
		return [
			(pointer_event.clientX - canvasContainer.clientLeft) / canvasContainer.clientWidth,
			(pointer_event.clientY - canvasContainer.clientTop) / canvasContainer.clientHeight,
		];
	};

	document.addEventListener("keydown", (keyboard_event) => {
		if (!menu_active) {
			bindings.process_key_press(keyboard_event.code);
		}
	});

	document.addEventListener("keyup", (keyboard_event) => {
		bindings.process_key_release(keyboard_event.code);
	});

	document.addEventListener("pointermove", (pointer_event) => {
		const pt = pointer_event_to_x_y(pointer_event);
		bindings.process_pointer_move(pt[0], pt[1]);
		// Counter-intuitively, a cursor's target being the menu Layer means that it's not actually on any part of the menu.
		if (pointer_event.target == menuLayer) {
			menu_active = false;
			(document.activeElement as HTMLElement)?.blur();
		}
	});

	document.addEventListener("contextmenu", (mouse_event) => {
		if (mouse_event.target == menuLayer) {
			mouse_event.preventDefault();
		}
	});

	document.addEventListener("pointerdown", (pointer_event) => {
		if (pointer_event.target == menuLayer) {
			dragging_from_outside_menu = true;
			bindings.process_pointer_button(pointer_event.button, true);
			pointer_event.preventDefault();
		} else {
			dragging_from_outside_menu = false;
		}
	});

	document.addEventListener("pointerup", (pointer_event) => {
		bindings.process_pointer_button(pointer_event.button, false);
		dragging_from_outside_menu = false;
	});

	// Keep game settings in Rust synced with settings in JS.
	$: {
		bindings.set_painting_brush_size(tilePaintingSettings.brushSize);
	}
	$: {
		bindings.set_painting_tile_type(tilePaintingSettings.primaryTileType, true);
	}
	$: {
		bindings.set_painting_tile_type(tilePaintingSettings.secondaryTileType, false);
	}

	let game_info = {};

	window.setInterval(() => {
		while (true) {
			const queue_val = bindings.pop_internal_event_queue();
			if (queue_val == null) {
				break;
			}
		}
		game_info["visible_chunks"] = bindings.get_visible_chunks();
		game_info["loaded_chunks"] = bindings.get_loaded_chunks();
		game_info["active_chunks"] = bindings.get_active_chunks();
		game_info["current_tick"] = bindings.get_current_tick();
		game_info["pending_tile_count"] = bindings.get_pending_tile_count();
		game_info["chunk_with_unsaved_changes_count"] = bindings.get_chunk_with_unsaved_changes_count();
		game_info["saved_chunk_count"] = bindings.get_saved_chunk_count();
		game_info["pending_tile_count"] = bindings.get_pending_tile_count();
	}, 16);
</script>

<main bind:this={main}>
	<div id="canvas-layer">
		<!-- NOTE: Winit prevents the canvas from being resized with CSS. -->
		<!-- canvas-container is instead used to inform Winit of what size the canvas should be. -->
		<div id="canvas-container" bind:this={canvasContainer} />
	</div>
	<div id="menu-layer" bind:this={menuLayer}>
		<Menu
			bind:game_info
			bind:tilePaintingSettings
			bind:tileTypes
			bind:active={menu_active}
			hide={dragging_from_outside_menu}
		/>
	</div>
</main>

<style>
	main {
		/* Self layout */
		box-sizing: border-box;
		height: 100%;
		width: 100%;

		/* Children layout */
		position: relative;
	}

	:global(body) {
		/* Self layout */
		padding: 0;

		/* Self style */
		background-color: #111;
	}

	:global(*) {
		box-sizing: border-box;
	}

	#canvas-layer {
		pointer-events: none;

		/* Self layout */
		position: absolute;
		width: 100%;
		height: 100%;

		/* Children layout */
		display: flex;
		align-items: stretch;
		justify-content: stretch;
	}

	#menu-layer {
		pointer-events: all;

		/* Self layout */
		position: absolute;
		width: 100%;
		height: 100%;

		/* Children layout */
		display: flex;
		flex-direction: row;
		justify-content: end;
		align-items: stretch;

		overflow-y: scroll;
	}

	#canvas-container {
		/* Self layout */
		flex-grow: 1;

		/* Canvas layout (this is not actually necessary, as the canvas size matches this contaienr's size) */
		display: flex;
		justify-content: center;
		align-items: center;

		readonly: readonly;
	}
</style>
