<script lang="ts">
	import type { TilePaintingSettings, TileType } from "../main";
	import MenuItemLabel from "./MenuItemLabel.svelte";
	import TextInput from "./TextInput.svelte";
	import TileTypeButton from "./TileTypeButton.svelte";

	export let tileTypes: TileType[];
	export let tilePaintingSettings: TilePaintingSettings;

	let search_term: string = "";

	let can_scroll_down = true;
	let can_scroll_up = false;

	const matches_search = (search_term: string, name: string) => {
		if (search_term.length == 0) {
			return true;
		}

		return name.toLowerCase().indexOf(search_term.toLowerCase()) >= 0;
	};

	let list = null;
	function on_scroll() {
		if (list != null) {
			const scroll_progress = list.scrollTop / (list.scrollHeight - list.clientHeight);
			can_scroll_down = scroll_progress < 1.0;
			can_scroll_up = scroll_progress > 0.0;
		}
	}

	onload = () => {
		on_scroll();
	};
</script>

<div id="outer">
	<MenuItemLabel name="Type" />
	<TextInput
		label={null}
		bind:value={search_term}
		placeholder="Search..."
		on_input={() => {
			// A timeout is used because scroll values are updated After the input function is called.
			window.setTimeout(on_scroll, 0);
		}}
	/>
	<div
		id="list"
		bind:this={list}
		class:can-scroll-down={can_scroll_down}
		class:can-scroll-up={can_scroll_up}
		on:scroll={on_scroll}
	>
		{#each tileTypes ?? [] as tileInfo}
			{#if matches_search(search_term, tileInfo.name)}
				<TileTypeButton bind:tilePaintingSettings {tileInfo} />
			{/if}
		{/each}
	</div>
</div>

<style>
	#outer {
		width: 100%;

		display: flex;
		flex-direction: column;
		justify-content: stretch;
		align-items: stretch;
		gap: 0.5em;
	}

	#list {
		display: flex;
		flex-direction: column;
		align-items: start;
		justify-content: start;
		overflow-y: scroll;

		max-height: 20em;
	}

	#list.can-scroll-up {
		box-shadow: inset 0 5em 1em -3em #111d;
	}
	#list.can-scroll-down {
		box-shadow: inset 0 -5em 1em -3em #111d;
	}
	#list.can-scroll-up.can-scroll-down {
		box-shadow: inset 0 5em 1em -3em #111d, inset 0 -5em 1em -3em #111d;
	}
</style>
